<template>
    <div class="Register">
      <HomePageTop :TopTitle="TopTotle"></HomePageTop>
      <div class="pl30 pr30">
        <img src="../../assets/img/common/logobox.png" class="RegisterLogo" >
        <!-- 注册部分 -->
        <div>
          <form action="" method="">
            <!-- 第一行 -->
            <div class="both filling-in border-bD8D8D8" >
              <span class="fillingLeft">手机号</span>
              <div class="fl pt30 pb30">
                <input type="text" placeholder="请输入手机号" autocomplete="off" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" @blur="judge1" v-model="AccountModel">
              </div>
              <span class="reminder" v-if="PhoneTrue">{{numberText}}</span>
            </div>
            <div class="both filling-in border-bD8D8D8">
              <span class="fl fillingLeft">确认密码</span>
              <div class="fl pt30 pb30">
                <input :type="PasswordAffirm==true?'password':'text'" v-model="AffirmPassword" placeholder="请输入密码"  @blur="judge2" name="NewPasswordNumber" autocomplete="off" >
              </div>
              <p class="icon_paaword affirm" @click="PasswordShow">
                <img :src="PasswordAffirm==true?passwordNo:passwordYes" alt="">
              </p>
              <span  class="reminder" v-if="PasswordTrue">{{passwordNumber}}</span>
            </div>
            <div class="both filling-in border-bD8D8D8">
              <span class="fl fillingLeft">验证码</span>
              <div class="fl pt30 pb30">
                <input type="password" placeholder="请输入验证码" autocomplete="off" maxlength="4" v-model="verifyModel" @blur="judge3">
              </div>
              <p class="icon-verification">
                <!--              <img src="../../Content/img/Register/verification.png" alt="">-->
                <img src="../../assets/img/Register/verification.png" alt="">
              </p>
              <span  class="reminder" v-if="VerifyTrue">请输入验证码</span>
            </div>
            <!-- 点击登录按钮 -->
            <div class="public-btn mt60 mb20" @click="submit">
              立即登录
            </div>
          </form>
          <div class="both f28 cl_333">
            <router-link to="Forget"  class="fl cl_333">忘记密码</router-link>
            <p class="dib fr">
              还没有账号?
              <router-link to="NewUser" class="cl_f90">立即注册</router-link>
            </p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    import Vue from "vue"
    import HomePageTop from "../common/HomePageTop";
    import $ from "jquery";

    export default {
        name: "Register",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle:{name:"会员登录",left:true},
              // 账号数据model
              AccountModel:"",
              //  确认密码model
              AffirmPassword:"",
              //  验证码model
              verifyModel:"",
              //确认密码切换图片
              PasswordAffirm:true,
              //  不可见图片切换图片
              passwordNo:require("../../assets/img/Register/passwordNo.png"),
              passwordYes:require("../../assets/img/Register/passwordYes.png"),
              //  手机号的错误弹窗
              PhoneTrue:false,
              numberText:"手机号不能为空",
              //密码错误弹窗
              PasswordTrue:false,
              passwordNumber:"密码不能为空",
              //验证码错误弹窗
              VerifyTrue:false,

            }
        },
        methods: {
          //登录
          submit(){
            if(this.VerifyTrue==false&&this.PasswordTrue==false&&this.PhoneTrue==false&&this.verifyModel!=""&&this.AffirmPassword!=""&&this.AccountModel!=""){
              alert("发起请求")
            }else{
              this.judge1();
              this.judge2();
              this.judge3();
            }
            // let myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
            // if(!myreg.test(this.AccountModel)){
            //   if(this.AccountModel==""){
            //     this.numberText="手机号不能为空"}else{this.numberText="请输入正确的格式"}
            //   this.PhoneTrue=true;
            // }else if(this.AffirmPassword!=""&&){
            //   console.log(asdasd);
            // }
          },
          //手机号判定弹窗
          // judge(i){
          //
          //   if(i==0){
          //
          //   }else if(i==1){
          //
          //   }else if(i==2){
          //
          //   }
          //
          // },
          judge1(){
            let myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
            if(!myreg.test(this.AccountModel)){
              if(this.AccountModel==""){
                this.numberText="手机号不能为空"}else{this.numberText="请输入正确的格式"}
              this.PhoneTrue=true;
            }else{
              this.PhoneTrue=false
            }},
          judge2(){if(this.AffirmPassword==""){
            this.passwordNumber="密码不能为空";
            this.PasswordTrue=true
          }else if(this.AffirmPassword.length<4){
            this.passwordNumber="密码数量不够";
            this.PasswordTrue=true
          }else{
            this.PasswordTrue=false
          }},
          judge3(){ if(this.verifyModel==""&&this.verifyModel.length<4){
            this.VerifyTrue=true;
          }else{
            this.VerifyTrue=false;
          }},




          PasswordShow() {
            this.PasswordAffirm == true ? this.PasswordAffirm = false : this.PasswordAffirm = true
            // i=="1"?():(this.PasswordAffirm==true?this.PasswordAffirm==false:this.PasswordAffirm==true)

          },
        },
        created() {

        }
    }

</script>

<style scoped>
  .Register {height: 100%;background: #fff}
  .Register .filling-in .icon_paaword img {
    width: 100%;
    vertical-align: middle;
  }
  .Register .filling-in .fillingLeft {
    color: #666666;
    font-size: 0.34rem;
    height: 100%;
    display: inline-block;
    line-height: 1rem;
    float: left;
    width: 1.40rem;
  }
  .Register .filling-in .fl input {
    border: none;
    color: #333333;
    font-size: 0.32rem;
    outline: none;
    vertical-align: text-bottom;
    padding-left: 0.3rem;
  }
html, body {background: #fff;}
</style>



